.alert-notes-container {
	.add-alert-note {
		align-items: center;
		color: $app-grey;
		display: flex;
		margin-left: auto;
		font-weight: $text-weight-semi-bold;
		outline: none;

		svg {
			height: 14px;
			padding-right: 4px;
			vertical-align: middle;
			width: 14px;
			box-sizing: unset;
		}
	}

	.alert-associated-notes {
		.note-list {
			.note-list-item {
				margin: 12px 0px;
				padding: 16px;
				border: 1px solid $border-light-color;
				border-radius: 8px;
				position: relative;
				box-shadow: 0px 1px 6px $list-item-shadow;
				background: $list-item-gradient;

				.note-actions {
					display: none;
					position: absolute;
					right: 4px;
					top: 8px;
					.btn {
						height: 32px;
						width: 32px;
						margin-left: auto;

						svg {
							fill: $secondary-text-color-1;
							height: 12px;
							width: 12px;
						}
					}
				}
				.note-info {
					display: flex;
					.avatar__icon {
						min-width: 32px;
						min-height: 32px;
					}
					.note-info-text {
						margin-left: 16px ;
						.note-user-link {
							display: flex;
							margin-bottom: 8px;
							.user-name {
								font-weight: 600;
								margin-right: 4px;
							}
						}
						.note-description {
							margin: 12px 0px;
							img {
								max-width: 100%;
							}
						}
					}
				}
				&:hover {
					.note-actions {
						display: inline-block;
					}
				}
			}
		}
	}

	.no-note {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-direction: column;
		height: 260px;
		margin-top: 50px;

		& > img {
			width: 200px;
			height: 200px;
		}

		.note-helptext {
			font-weight: $text-weight-medium;
			width: 46%;
			text-align: center;
		}
	}
}

.note-association-modal {
	.has-error {
		.froala-editor-container  {
			border: 1px solid $app-error-state;
			border-radius: 4px;
		}
	}
	.add-note-action {
		display: flex;
		float: right;
		margin-top: 16px;
		&.add-and-resolve {
			.ember-basic-dropdown {
				display: flex;

				.ember-basic-dropdown-trigger {
					margin-left: -14px;
					svg {
						fill: $app-white;
					}
				}
				.resolve-alert-content{
					margin-top: -8px;
					width: 224px;
					.basic-dropdown-item:hover {
						background-color: $dropdown-option-selected;
					}
				}
			}
		}
	}
}
