$alert-details-table-columns-width: 100px, 220px;

&.alerts_show {
	min-height: 700px;

	.page-title {
		.push-right {
			margin-left: auto;
		}

		.ember-badge + .bulk-action-options {
			margin-left: initial;
		}

		.ember-badge {
			margin-right: 8px;
			text-transform: capitalize;
			&.severity-ok {
				text-transform: uppercase;
			}
		}

		.btn-icon {
			margin-right: 0;
			&.active {
				background: $primary-text-color-1;
				svg {
					fill: $app-white;
				}
			}
		}
	}

	.page-content {
		background: $app-white;

		.dot-loading {
			text-align: center;
		}

		.cell-alert-severity-item .ember-badge{
			text-transform: capitalize;
			&.severity-ok {
				text-transform: uppercase;
			}
		}

		.tabs {
			border: none;
			&__items {
				border-bottom: 1px solid $app-border-primary;
			}

			&__item {
				border: none;
				&__nav {
					margin-right: 24px;
				}
			}
			.alertLogs-plus {
				.has-content-count::after {
					content: attr(data-count) '+';
				}
			}
			#activities {
				.tab-contents {
					padding: 24px 72px 0px 0px;

					.avatar__icon, .avatar__initial {
						cursor: auto;
					}
					.no-activities {
						display: flex;
						align-items: center;
						justify-content: space-between;
						flex-direction: column;
						height: 260px;
						margin-top: 50px;
						& > img {
							width: 200px;
							height: 200px;
						}
						.no-activities-helptext {
							font-weight: $text-weight-medium;
							width: 46%;
							text-align: center;
						}
					}
				}
			}
		}

		.lt-line-clamp__more, .lt-line-clamp__less {
			display: block;
		}

		.alert-details-header {
			padding: 20px 20px 0;

			& > h3, div {
				margin-bottom: 8px;
			}

			.subject {
				font-size: $text-large;
				font-weight: $text-weight-semi-bold;
				word-break: break-word;
			}

			.alert-datetime-properties {
				font-size: $text-normal;
				color: $secondary-text-color-1;
			}

			.description {
				word-break: break-word;
			}
		}

		:lang(sv-SE) { 
			$alert-details-table-columns-width: 120px, 220px;  
		}

		.ember-light-table {
			$table-row-types: 'header', 'item';
			$table-columns: 'severity', 'date';

			@each $type in $table-row-types {
				@each $col in $table-columns {
					$i: index($table-columns, $col);

					.cell-alert-#{$col}-#{$type} {
						width: nth($alert-details-table-columns-width, $i);
					}
				}
			}

			.cell-alert-value-item, .cell-alert-additional-info-item {
				color: $app-form-label-color;
			}

			.cell-alert-message-item, .cell-alert-value-item, .cell-alert-additional-info-item {
				.lt-line-clamp__raw-line {
					word-wrap: break-word;
				}
			}
		}

		@import "associated-incident";
		.no-association {
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-direction: column;
			height: 270px;
			margin-top: 50px;

			& > img {
				width: 200px;
				height: 200px;
			}

			.helptext {
				font-weight: $text-weight-medium;
				width: 46%;
				text-align: center;
			}
		}

		.associate-ticket {
			display: flex;
			align-items: center;
			margin-top: 8px;

			&.disabled {
				float: right;
				margin-bottom: 20px;

				a {
					color: $disabled-text-color;
					font-weight: $text-weight-medium;
				}

				svg {
					.add-outer {
						fill: $white;
						stroke: $disabled-text-color;
					}

					.add-sign {
						fill: $disabled-text-color;
					}
				}
			}

			svg {
				width: 14px;
				height: 14px;
				margin-right: 4px;
			}
		}
	}

	.page-sidebar {
		border-left: 1px solid $border-dark-color;
		background: $app-sidebar-bg-lighter;

		.alert-properties {
			padding: 16px;
			h4 {
				font-size: initial;
				font-weight: $text-weight-semi-bold;
				margin-bottom: 16px;
			}

			.ember-badge {
				margin-bottom: 4px;
			}

			.alert-severity .ember-badge {
				text-transform: capitalize;
				&.severity-ok {
					text-transform: uppercase;
				}
			}

			.btn-mini {
				display: flex;
				align-items: center;
				background: none;
				padding-right: 4px;

				svg {
					width: 14px;
					height: 14px;
				}

				&.disabled svg{
					fill: $disabled-text-color;
				}
			}

			label {
				font-size: $text-normal;
				font-weight: $text-weight-medium;
				color: $secondary-text-color-1;
			}

			.prop-item-row {
				margin-bottom: 16px;
				> span {
					font-weight: $text-weight-medium;
					overflow-wrap: break-word;
				}
				.ember-badge {
					word-break: break-all;
				}
			}
		}
	}
}

// TODO: Remove this css after implemented the 
.fs-tl-container {
	margin: auto;
	display: block;
	position: relative;

	.fs-tl {
		padding: 0;

		.fs-tl-item {
			list-style: none;
			margin: auto;
			margin-left: 184px;
			min-height: 32px;
			border-left: 2px solid $border-light-color;
			padding: 0 0 28px 36px;
			position: relative;

			&:last-child {
				border-left: 0;
			}

			&.fs-tl-more:last-child {
				border-left: 2px solid $border-light-color;
			}

			&.fs-tl-item-marker {
				.fs-tl-item-time {
					font-weight: 600;
					top: -4px;
				}

				.fs-tl-item-icon.fs-tl-item-icon-empty {
					top: 0px;
					background: $app-warning-state;
					width: 10px;
					height: 10px;
					left: -6px;
					border-radius: 50%;
				}
			}

			.fs-tl-item-time {
				position: absolute;
				left: -212px;
				text-align: right;
				top: 8px;
				color: $app-grey;
				min-width: 172px;
			}

			.fs-tl-item-icon {
				position: absolute;
				left: -17px;

				&.fs-tl-item-icon-empty {
					top: 12px;
					background: $secondary-text-color-2;
					width: 10px;
					height: 10px;
					left: -6px;
					border-radius: 50%;
				}

				.avatar-icon {
					background: $section-background-blue;

					svg {
						height: 20px;
						width: 20px;
					}
				}
			}

			.fs-tl-item-body {
				.fs-tl-item-title {
					font-weight: 500;
					margin-bottom: 8px;
					a {
						color: $app-grey;
					}
				}

				.fs-tl-item-desc {
					background: #FEFEFE;
					border: 1px solid $border-light-color;
					box-sizing: border-box;
					border-radius: 4px;
					padding: 16px;
					color: $toggleSlider;
					font-weight: 500;

					b {
						color: $app-grey;
					}

					.list-title {
						margin-bottom: 12px;

						&:not(:first-child) {
							margin-top: 16px;
						}
					}

					.list-items {
						li {
							padding: 2px 0px;
							margin-bottom: 4px;

							&::before {
								content: "\2022"; // Circle bullet charactor
								color: $secondary-text-color-2;
								display: inline-block;
								width: 16px;
								font-size: 16px;
								line-height: 16px;
								font-weight: 700;
							}
						}
					}
				}
			}
		}
	}
}