&.alerts_index_alert_ingestion {
	.page-layout-content .page-content {
		background-color: $app-white;
		padding: 16px;
	}

	//Overwriting Light Table CSS for new empty state, When the alerts table is empty.
	.alerts-table.empty {
		.ember-light-table {
			background-color: transparent;
			border: none;

			.lt-body-wrap {
				overflow: hidden;

				tbody.lt-body {
					tr.lt-no-data > td > div {
						margin: 100px auto;
					}
					tr.lt-row:not(.is-selected):not(.selected) {
						background-color: transparent;
					}
				}
			}

			.lt-body-wrap > .lt-head > table > .lt-head {
				background-color: $background-color;

				th {
					color: $disabled-text-color;

					path {
						fill: $disabled-text-color;
					}
				}
			}
		}
	}
}

// When alert list is empty
.alert-list-empty-card {
	@include inline-flexbox;
	height: 320px;
	padding: 24px;
	text-align: left;
	background-color: $app-white;
	border: 1px solid $border-light-color;
	border-radius: 8px;
	box-shadow: 0px 1px 6px $box-shadow-color-1;

	.btn{
		margin-top: 20px;
	}

	.link-with-icon {
		@include flexbox;
		@include align-items(center);
		gap: 8px;
	}

	p,
	a {
		@include font-size($text-normal);
		margin: 0px;
	}

	p {
		margin-top: 32px;
		width: 352px;
	}

	a {
		font-weight: $text-weight-semi-bold;
		color: $linkColor;
	}

	.title {
		@include font-size($title-normal);
		font-weight: $text-weight-bold;
		margin-bottom: 16px;
	}

	.knw-more-link > svg {
		width: 10px;
		height: 10px;
	}

	.add-int-link {
		margin-top: 16px;
		a {
			@include font-size($title-small);
		}
	}

	.img-container {
		min-width: 472px;
		img {
			margin-top: 28px;
		}
	}

	&.add-integration{
		.img-container {
			img {
				height: 208px;
			}
		}
	}

}
