&.alerts_index {
	min-height: 700px;

	.sticky-element-container {
		.sticky-element { box-sizing: border-box; height: 48px; }

		//.sticky-element--sticky-top { z-index: 999; }
	}

	.page_layout__title-bar {
		.page-title {
			justify-content: space-between;

			.title {
				font-size: 18px;
				font-weight: 500;
			}

			.filter-icon {
				box-sizing: border-box;
				height: 32px;
				margin-right: 0;
				padding: 8px;
				width: 32px;

				svg { height: 16px; width: 16px; }

				&:active, &.active {
					background: $btnPrimaryBackgroundHighlight;

					svg path{
						fill: $app-icon-fillcolor-active;
					}
				}

				&.filter-active { border: 1px solid $primary-text-color-1; }
			}
		}
	}

	.page-layout-content {
		display: flex;

		.page-content {
			max-width: calc(100% - 32px);

			.alerts-table-options {
				align-items: center;
				display: flex;
				height: 32px;
				justify-content: space-between;

				.select-all-btn {
					input[type='checkbox'] {
						margin-top: -2px;
						margin-left: 16px;
					}

					label {
						padding-left: 8px;
					}
				}

				.bulk-action-options {
					margin-left: 0;
				}
			}

			.alerts-table {
				margin-top: 8px;

				.table-wrap {
					.ember-light-table {
						@include set-list-table-cell-widths();

						.lt-head {
							.cell-alert-message-header {
								padding-left: 45px;
								width: 238px;
							}
						}
						.lt-row {
							.cell-alert-message-item {
								&:first-letter { text-transform: uppercase; }
								.cell {
									display: flex;
									position: relative;
									.tooltip-wrapper {
										margin-top: 2px;
									}
									.auto-update-icon {
										margin-top: 6px;
									}
									.visual-hide {
										opacity: 0;
									}
								}
							}

							.cell-alert-severity-item {
								@include set-severity-tags();

								[class^='state'] span {
									text-transform: capitalize;
								}

								.state-ok span.ok {
									text-transform: uppercase;
								}
							}

							.tooltip-wrapper { // added for message, hostname column
								width: inherit;
								@extend .ellipsis
							}
						}
					}

					// --> .ember-light-table

					.ember-pagination .pagination__arrow {
						padding: 0px;
					}

					.column-customizer {
						.ember-basic-dropdown .btns-wrapper{
							flex-wrap: wrap;
							width: 260px;

							.btn {
								margin-bottom: 8px;
								max-width: 240px;
								min-height: 32px;
								height: auto;
								white-space: normal;
								word-wrap: break-word;
								
								&-primary { 
									margin-left: 8px; 
									margin-right: 0; 
								}
			
								&-secondary { margin-right: 0; }
							}
						}
					}
				}

				// --> .table-wrap
			}

			// --> .alerts-table

			// Styles specific to opened sidebar case
			&.sidebar-open {
				// .column-customizer { right: 310px }

				.ember-light-table { max-width: 100% !important; }

				.ember-pagination {
					margin-left: 1px !important;
					margin-right: -1px !important;
				}
			}

		}

		// --> .page-content
		.page-sidebar {
			padding: 0 !important;
			min-height: calc(100vh - 116px);

			.title { padding: 16px; }

			.filter-row {
				margin-bottom: 8px;
				padding: 0 16px;

				.title-small {
					padding-bottom: 5px;
					padding-top: 8px;

					&:first-letter { text-transform: uppercase; }
				}

				.ember-basic-dropdown {
					.ember-power-select-trigger {
						box-sizing: border-box;

						.ember-power-select-status-icon {
							left: auto;
							position: absolute !important;
							right: 4px;
						}

						.ember-power-select-trigger-multiple-input:focus { border: none; }
					}

					.ember-power-select-single-list > .ember-power-select-option[aria-selected="true"] { color: $linkColor; }

					.ember-basic-dropdown-content .ember-power-select-options { padding: 0; }

					// hide chevron for multi select
					.ember-power-select-multiple-trigger .ember-power-select-status-icon { display: none; }

					// hide already selected items from dropdown
					.ember-power-select-multiple-list > .ember-power-select-option[aria-selected="true"] { display: none; }
				}
			}
			.filter-row-child {
				margin-left: 17px;
				border-left: 1px solid $app-border-primary;
			}

			.btns-wrapper {
				margin-top: 24px;
				padding: 0 16px;

				.btn {
					border-radius: 3px;
					float: right;
					margin-left: 0;
					outline: none;
					margin-bottom: 8px;
					max-width: 260px;
					min-height: 32px;
					height: auto;
					white-space: normal;
					word-wrap: break-word;
					
					&-primary { 
						margin-left: 8px; 
						margin-right: 0; 
					}

					&-secondary { margin-right: 0; }
				}
			}
		}

		// --> .page-sidebar
	}

	.alerts-list-default { /* Default content style when ams config is not enabled */
		.default-content {
			margin-top: 50px;
			text-align: center;

			h3, p { padding: 20px; }
		}
	}

	.ui-component-refresh-notification {
		margin-top: 100px;
	}
}

// end of '.alerts_index' styles
